// theme-color
//. end-theme-color

*, *::before, *::after {
	box-sizing: border-box;
}
  
$breakpoints: (
	"(min-width: 768px)" "med",
	"(min-width: 1200px)" "large"
);
  
@mixin breakpoint($point) {
	@each $breakpoint in $breakpoints {
		$query: nth($breakpoint, 1);
		$name: nth($breakpoint, 2);
		@if ($name == $point) {
		@media #{$query} { @content }
		}
	}
}

