}

.site {
	display: flex;
	flex-direction: column;
	min-height: 100%;
}

.site-content {
	flex: 1;
	width: 100%;
	padding: 0 1em;
	/* IE10 tweener syntax */
	-ms-flex-preferred-size: auto;
	@include breakpoint("med") {
		padding: 0 1.5em;
	}
	@include breakpoint("large") {
		
	}
}

.container {
	max-width: 1400px;
	margin: 0 auto;
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}

.grid {
	display: flex;
	flex-wrap: wrap;
	list-style: none;
	margin: 0;
	padding: 0;
	padding-bottom: 12px;
}

.grid-cell {
	flex: 1;
}

.grid--flexCells {
	& > .grid-cell {
		display: flex;
	}
}

.grid--top {
	align-items: flex-start;
}

.grid--bottom {
	align-items: flex-end;
}

.grid--center {
	align-items: center;
}

.grid-cell--top {
	align-self: flex-start;
}

.grid-cell--bottom {
	align-self: flex-end;
}

.grid-cell--center {
	align-self: center;
}

@mixin grid-sizing($name: "") {
	$sep: "-";
	@if $name == "" {
		$sep: "";
	}
	.#{$name}#{$sep}grid--fit > .grid-cell {
		flex: 1;
	}
	.#{$name}#{$sep}grid--full > .grid-cell {
		flex: 0 0 100%;
	}
	.#{$name}#{$sep}grid--1of2 > .grid-cell {
		flex: 0 0 100%
	}
	.#{$name}#{$sep}grid--1of3 > .grid-cell {
		flex: 0 0 33.3333%
	}
	.#{$name}#{$sep}grid--1of4 > .grid-cell {
		flex: 0 0 25%
	}
	@include breakpoint("med") {
		
	}
}

// default grid-sizing modifiers
@include grid-sizing();

// grid-sizing modifiers for each breakpoint
@each $breakpoint in $breakpoints {
$name: nth($breakpoint, 2);
@include breakpoint($name) {
	@include grid-sizing($name);
}
}

@mixin grid-gutters($name: "") {
$sep: "-";
@if $name == "" {
	$sep: "";
}
.#{$name}#{$sep}grid--gutters {
	margin-left: -1em;
	& > .grid-cell {
	padding-left: 1em;
	}
}
.#{$name}#{$sep}grid--guttersLarge {
	margin-left: -1.5em;
	& > .grid-cell {
	padding-left: 1.5em;
	}
}
.#{$name}#{$sep}grid--gutters2x {
	margin-left: -2em;
	& > .grid-cell {
	padding-left: 2em;
	}
}
.#{$name}#{$sep}grid--gutters3x {
	margin-left: -3em;
	& > .grid-cell {
	padding-left: 3em;
	}
}
}

// create the default grid-gutters modifiers
@include grid-gutters();

// create grid-gutters modifiers for each breakpoint
@each $breakpoint in $breakpoints {
$name: nth($breakpoint, 2);
@include breakpoint($name) {
	@include grid-gutters($name);
}
}

// Firefox doesn't yet support multiline Flexboxes
// https://bugzilla.mozilla.org/show_bug.cgi?id=702508
@supports not (flex-wrap: wrap) {
.grid--ffMultilineSupport {
	display: block;
	& > .grid-cell {
	display: inline-block;
	vertical-align: top;
	}
}
@mixin grid-sizing($name: "") {
	$sep: "-";
	@if $name == "" {
	$sep: "";
	}
	.#{$name}#{$sep}grid--full {
	display: block;
	& > .grid-cell {
		display: block;
	}
	}
	.#{$name}#{$sep}grid--fit,
	.#{$name}#{$sep}grid--1of2,
	.#{$name}#{$sep}grid--1of3,
	.#{$name}#{$sep}grid--1of4 {
	display: flex;
	}
}
@include grid-sizing();
@each $breakpoint in $breakpoints {
	$name: nth($breakpoint, 2);
	@include breakpoint($name) {
	@include grid-sizing($name);
	}
}
}

@mixin sizes($name: "") {
	$sep: "-";
	@if $name == "" {
		$sep: "";
	}
	.colum-#{$name}#{$sep}full {
		width: 100%;
		flex: none;
	}
	.colum-#{$name}#{$sep}1of2 {
		width: 100%;
		flex: none;
	}
	.colum-#{$name}#{$sep}1of3 {
		width: 100%;
		flex: none;
	}
	.colum-#{$name}#{$sep}2of3 {
		width: 100%;
		flex: none;
	}
	.colum-#{$name}#{$sep}1of4 {
		width: 100%;
		flex: none;
	}
	.colum-#{$name}#{$sep}3of4 {
		width: 100%;
		flex: none;
	}
	
	@include breakpoint("med") {
		.colum-#{$name}#{$sep}1of2 {
			width: 50%;
			flex: none;
		}
		.colum-#{$name}#{$sep}1of3 {
			width: 33.3333%;
			flex: none;
		}
		.colum-#{$name}#{$sep}2of3 {
			width: 66.6667%;
			flex: none;
		}
		.colum-#{$name}#{$sep}1of4 {
			width: 33.3333%;
			flex: none;
		}
		.colum-#{$name}#{$sep}3of4 {
			width: 75%;
			flex: none;
		}
	}

	@include breakpoint("large") {
		.colum-#{$name}#{$sep}1of2 {
			width: 50%;
			flex: none;
		}
		.colum-#{$name}#{$sep}1of3 {
			width: 33.3333%;
			flex: none;
		}
		.colum-#{$name}#{$sep}2of3 {
			width: 66.6667%;
			flex: none;
		}
		.colum-#{$name}#{$sep}1of4 {
			width: 25%;
			flex: none;
		}
		.colum-#{$name}#{$sep}3of4 {
			width: 75%;
			flex: none;
		}
	}
};

// create global sizes
@include sizes();

// create sizes for each breakpoint
@each $breakpoint in $breakpoints {
	$name: nth($breakpoint, 2);
	@include breakpoint($name) {
		@include sizes($name);
	}
}

