//+ form
.form-container{
	margin-top: 15px;
	margin-bottom: 36px;
}


form{
	display: block;
}

label{
    width: 100%;
}

button, input, optgroup, select, textarea { // reset styles otherwise autofocus and autocomplete will be buggy
    font-family: inherit;
    font-size: inherit;
    font-style: inherit;
    font-weight: inherit;
    line-height: inherit;
}

input, textarea{
	color: $txt-read;
	display: block;
    width: 100%;
    border: $bg-highlighter solid 1px;
    border-radius: 5px;
    background-color: $bg-highlighter;
    padding: 11px 19px;
	margin-bottom: 12px;
	&:focus{
		outline: none; // disable outline(border around <input>) if you want to change the color
		border-color: $violet-primary; // replace outline border on <input> when focus is true
	}
	&:-webkit-autofill::first-line { // css for <input> text when autocomplete options are selected
		font-family: 'Poppins', helvetica neue,Helvetica,Arial,sans-serif;
		font-weight: 500;
		font-size: 16px;
		line-height: 24px;
	  }
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
	color: red;
	border: 1px solid $blue-link; // change outline of <input> when autocomplete is true
	-webkit-text-fill-color: $txt-read; // change text color when autocomplete is true
	-webkit-box-shadow: 0 0 0px 1000px $bg-highlighter inset; // change bg-color of autofill when autocomplete is true
	transition: background-color 5000s ease-in-out 0s;
}

input#file-input{
	display: none;
}

input#file-input + label{
	cursor: pointer;
	color: $txt-read;
	text-align: center;
	display: block;
    width: 100%;
    border: $bg-highlighter solid 1px;
	border-radius: 5px;
	border: solid 1px $bg-highlighter;
    background-color: $bg-highlighter;
    padding: 11px 19px;
	margin-bottom: 12px;
	&:hover{
		border-color: $violet-primary;
	}
}

*::-webkit-input-placeholder {
    color: $txt-sub;
}

:-moz-placeholder {
    color: $txt-sub;
    opacity: 1;
}

::-moz-placeholder {
    color: $txt-sub;
    opacity: 1;
}

:-ms-input-placeholder {
    color: $txt-sub;
}

::-ms-input-placeholder {
    color: $txt-sub;
}

::placeholder {
    color: $txt-sub;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}
//. end-form